<script>
import { reactive, toRefs } from "vue";
import { getLang, setLang } from "@/utils/i18n"
export default {
  components: {
  },
  setup() {
    const data = reactive({
      local: getLang().toLowerCase(),
      changeLanuage: (val) => {
        setLang(val)
        data.local = val
      },
    });
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
};
</script>

<style lang="scss">
.header{
  max-width: 1200px;
  margin: 0 auto;
}
.language{
  justify-content: flex-end;
  padding-right: 48px;
  .el-dropdown{
    color: #75809F;
    font-size: 16px;
  }
  .mobile-language {
    display: none;
  }
  .mobile-menu {
    display: none;
    height: 100%;
    .nav-icon {
      margin: 36px 0 0 10px;
      display: inline-block;
      position: relative;
      width: 20px;
      height: 2px;
      background-color: #DFE0E2;
      transition-property: background-color, transform;
      transition-duration: 300ms;
      &:before,
      &:after {
        content: '';
        display: block;
        width: 100%;
        height: inherit;
        position: absolute;
        background:  #DFE0E2;
        transition-property: margin, transform;
        transition-duration: 300ms;
      }
      &:before {
        margin-top: -8px;
      }
      &:after {
        margin-top: 8px;
      }
      &.active {
        background: rgba(0, 0, 0, 0);
        &:before {
          margin-top: 0;
          transform: rotate(45deg);
        }
        &:after {
          margin-top: 0;
          transform: rotate(-45deg);
        }
      }
    }
  }
}
.el-dropdown-menu__item.active{
  color: #0264C3;
}
.el-dropdown-menu__item:focus, .el-dropdown-menu__item:not(.is-disabled):hover {
    background-color: rgba($color: #0264C3, $alpha: 0.1) !important;
    color: #0264C3 !important;
}
.theme-light {
  .language{
    .el-dropdown{
      color: #333;
    }
  }
  .mobile-menu {
    .nav-icon {
      background-color: #333;
      &:before,
      &:after {
        background:  #333;
      }
    }
  }
}
@media screen and (max-width: 480px) {
  .language {
    padding-right: 12px;
    .el-dropdown{
      font-size: 18px;
    }
    .pc-language {
      display: none;
    }
    .mobile-language {
      display: block;
    }
    .mobile-menu {
      display: block;
    }
  }
}
.el-dropdown-link{
    background: #272727;
    padding: 8px;
    border-radius: 50%;
    margin-right: 30px;
  img{
    width: 25px;
    height: 25px;
    display: block;
    border-radius: 50%;
  }
}
@media screen and (max-width: 480px) {
